import React from 'react';
import { Table } from 'react-bootstrap';
// import { propTypes } from 'react-bootstrap/esm/Image';
import { connect, useSelector, useDispatch} from 'react-redux'
import { useHistory } from 'react-router-dom'
import './App.css'

function Cart(props) {
    let state = useSelector((state)=> state)
    let dispatch = useDispatch();
    let history =useHistory();

return(
    <div className="w-800">
        <Table striped bordered hover>
            <thead>
                <tr>
                <th>상품번호</th>
                <th>상품명</th>
                <th>수량</th>
                <th>변경</th>
                </tr>
            </thead>
            <tbody>
                {state.map((listItem, i)=>{
                return(
                    <tr key={i}>
                        <td>{ listItem.id }</td>
                        <td>{ listItem.name }</td>
                        <td>{ listItem.quan }</td>
                        <td><button className="button-quan" onClick={()=>{ 
                            dispatch( { 
                                type: '수량증가', item : listItem.id }) }}>+
                            </button>
                        <button className="button-quan" onClick={()=>{ 
                            dispatch( {
                                 type: '수량감소', item : listItem.id }) }}>-
                            </button>
                        </td> 
                    </tr>
                )
                })
                }
            </tbody>
        </Table>
        {
            state.length===0
            ? (
                <div>장바구니에 상품이 없습니다. 상품을 담아보세요.</div>
            )
            :null
        }
        <button className="btn btn-danger">구매하기</button>
        <button className="btn btn-danger" onClick={()=>{history.push('/bodyandbath')}}>더 둘러보기</button>

    </div>
)
}

function state를props화(state) {
    return {
        작명 : state.reducer,
        alert열렸니 : state.reducer2,
}
}
// redux store 데이터 가져와서 props로 변환해주는 함수

export default connect(state를props화)(Cart);
// export default Cart